// Import utilities
import { itemsObject, isDefined } from '@builder/common/utilities';

/**
 * Determines whether Automator should make the item ID visible for some items
 * This modifies UncannyAutomator.recipe.items to add the "shouldShowItemId" property
 * 
 * @return {undefined}
 */
const flagItemsWithVisibleID = () => {
	// For now, only triggers can show IDs. Check if we should show it
	if ( recipeHasMultipleTriggersFromSameIntegration() ) {
		// Get the list of triggers
		const triggers = itemsObject( 'get', {
			filter: {
				itemType: 'trigger'
			}
		});

		// Iterate the items
		Object.entries( triggers ).forEach( ( [ triggerId, trigger ] ) => {
			// Add the property
			UncannyAutomator.recipe.items[ triggerId ].shouldShowItemId = true;
		} );
	}
}

/**
 * Checks if the recipe has multiple triggers from the same integration
 * To check this, we will check if there are more triggers than integrations used in triggers metabox
 * 
 * @return {Boolean} True if the recipe has multiple triggers from the same integration
 */
const recipeHasMultipleTriggersFromSameIntegration = () => {
	// Default response
	let response = false;

	// Get the list of triggers
	// Convert the object returned by itemsObject into an array
	const triggers = Object.values(
		itemsObject( 'get', {
			filter: {
				itemType: 'trigger'
			}
		})
	);

	// Check if there is more than one trigger
	if ( triggers.length > 1 ) {
		// Get the list of integrations used in the recipe triggers
		const integrationsInTriggers = [
			// Remove the duplicates using Set
			// Ex: [ 'LD', 'WC', 'LD' ] ==> [ 'LD', 'WC' ]
			...new Set(
				// Create an array of integration codes. Ex: [ 'LD', 'WC', 'LD' ]
				triggers.map( trigger => trigger.integrationCode )
			)
		];

		// Check if there is more than one trigger from same integration
		// To check this, we will check if there are more triggers than integrations
		if ( integrationsInTriggers.length < triggers.length ) {
			// Show the item ID
			// We don't want users to confuse tokens added by triggers from the same integration
			response = true;
		}
	}

	return response;
}

export default flagItemsWithVisibleID;
